@import url(https://fonts.googleapis.com/css2?family=Gloria+Hallelujah&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cousine&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{background:radial-gradient(ellipse at top, #2d534a 0%, #253032 70%);min-height:100vh}header{height:auto;padding-bottom:0.8rem;padding-left:1rem;background-color:#19201e;display:flex;justify-content:space-around}header h1{font-size:2.5em;margin:auto;color:#3eb37c;text-align:center;font-family:"Gloria Hallelujah",cursive;letter-spacing:2px;font-weight:10}header h1:hover{color:#42c085}.Nav{background:linear-gradient(170deg, rgba(16,29,23,0.85) 20%, rgba(0,0,0,0) 70%);display:flex;height:6rem;padding:1rem;padding-left:2rem}.QuoteBox{flex-grow:1;margin:auto;font-weight:600;overflow:hidden}.QuoteBox p{margin:auto 0;color:#3eb37c;font-size:1.4em}.QuoteBox p:last-child{color:#4b6863;font-size:1em}form{align-items:center;display:flex;padding:0}form button{padding:0.2em;height:2.2em;color:#42c085;background-color:#19201e;border-color:#19201e;border-radius:1.5em;border-style:solid;outline:none;border-top-left-radius:0;border-bottom-left-radius:0;background-color:#19201e;text-align:center;width:2.5em;background-image:url(data:image/png;base64,);background-repeat:no-repeat;background-size:65%;background-position:0.2em 0.3em}form button:active{background-color:#212524}form button:hover{cursor:pointer;background-color:#212524}form button:hover{background-color:#42c085;border-color:#42c085;color:#19201e;background-image:url(data:image/png;base64,)}form button:active{background-color:#42c085;border-color:#134b32}input{padding:0.2em;height:2.2em;color:#42c085;background-color:#19201e;border-color:#19201e;border-radius:1.5em;border-style:solid;outline:none;border-top-right-radius:0;-o-border-top-right-radius:0;-webkit-border-top-right-radius:0;border-bottom-right-radius:0;-o-border-bottom-right-radius:0;-webkit-border-bottom-right-radius:0;border-radius:0;border-top-left-radius:1.5em;border-bottom-left-radius:1.5em;width:5em;transition:width 0.3s ease-in-out}input:active{background-color:#212524}input:hover{cursor:pointer;background-color:#212524}input:focus{width:9em}select{-moz-appearance:none;appearance:none;-o-appearance:none;-webkit-appearance:none;padding:0.2em;height:2.2em;color:#42c085;background-color:#19201e;border-color:#19201e;border-radius:1.5em;border-style:solid;outline:none;text-align:center;margin:auto;width:4.3em;border-radius:0;background-image:url(data:image/png;base64,);background-size:1em 1em;background-repeat:no-repeat;background-position:3.2em 0.5em}select:active{background-color:#212524}select:hover{cursor:pointer;background-color:#212524}select:last-of-type{background-position:2.6em 0.5em;width:4.1em}content,.ErrorBox{width:97%;margin:auto;display:flex;flex-direction:column;align-items:center;justify-content:space-between;height:auto;min-height:77vh}content .Stretch,.ErrorBox .Stretch{flex-grow:1}.ChangePageBox{margin:1em auto;width:16em;display:flex;justify-content:space-evenly}.ChangePageBox button{padding:0.2em;height:2.2em;color:#42c085;background-color:#19201e;border-color:#19201e;border-radius:1.5em;border-style:solid;outline:none;width:4.5em;margin:auto 0;background-color:#19201e}.ChangePageBox button:active{background-color:#212524}.ChangePageBox button:hover{cursor:pointer;background-color:#212524}.ChangePageBox button:active{border-color:#134b32;box-shadow:inset rgba(2,29,20,0.842) 2px 2px 8px}.ChangePageBox button:disabled{border:none;box-shadow:none;color:#134b32;background-color:#19201e}.ChangePageBox p{padding:0.2em;height:2.2em;color:#42c085;background-color:#19201e;border-color:#19201e;border-radius:1.5em;border-style:solid;outline:none;width:6em;padding-top:0.3em;text-align:center;display:inline-block;margin:auto 0;position:inherit}.ChangePageBox p:active{background-color:#212524}.ChangePageBox p:hover{cursor:pointer;background-color:#212524}.ChangePageBox p:hover{box-shadow:none;border-color:#19201e;background-color:#19201e;cursor:default}.CardList{margin:0 auto;display:flex;flex-wrap:wrap;justify-content:center}.Card{background-color:rgba(24,30,26,0.8);color:#4b6863;width:22em;height:9em;margin:0.4em;border-radius:0.5em;display:flex;font-size:0.9em}.Card img{width:9em;min-width:9em;height:9em;flex-shrink:0;border-top-left-radius:0.5em;border-bottom-left-radius:0.5em}.Card div{padding:0.3em 0.1em 0.3em 0.4em;font-size:1em;display:flex;flex-direction:column;justify-content:space-between;font-family:'Cousine', monospace}.Card div i{font-weight:400;font-size:0.8em;color:#4b6863}.Card div h3{margin:0;color:#42c085;width:auto;height:auto;padding-right:0.2em}.Card div h4{color:#52927a;margin:auto 0}.Card div h4 i{font-size:0.9em;margin-left:0.1em}.Card div p{display:flex;flex-direction:column;margin-top:auto;margin-bottom:0;font-size:0.8em}.Card div p i{color:#52927a;font-size:1.1em;margin-left:0.4em;margin-top:0.1em}.Card .DeadSign{margin:0.4em 2.4em;width:7em;height:7em;border-color:#52927a 0.5;background-color:#52927a;border-radius:50%;border-style:solid;border-width:0.3em;background-image:url(data:image/png;base64,);background-size:cover;position:absolute;opacity:0.1}.FooterBox{background-color:#19201e;width:100%;padding:1rem;margin-top:auto;margin-bottom:0;align-self:flex-end;position:absolute}.FooterBox p{color:#4dad80;text-align:center;padding:0.2em;margin:0.3em auto}.FooterBox p a{font-style:italic;color:#52927a}.FooterBox p a:hover{color:#42c085}@media (min-width: 1920px){content,.ErrorBox,form,.Nav,header{font-size:1.2rem}}@media (min-width: 1500px){content,.ErrorBox,form,.Nav,header{font-size:1.1rem}}@media (max-width: 1000px){.Nav{height:8.5rem}}@media (max-width: 900px){.Nav{height:9.5rem}content,.ErrorBox,form,.Nav{font-size:0.95rem}}@media (max-width: 667px){.Nav{flex-wrap:wrap;flex-direction:column;justify-content:center;height:10rem;font-size:0.9rem;padding:0.2rem}.QuoteBox{flex-grow:0;width:90vw;margin:auto}form{margin:0 auto;padding:0}form select,form input,form button{margin:0}.ChangePageBox{margin:0.2em auto;width:20em;margin-top:0.3em}content,.ErrorBox,header{font-size:0.85rem}}@media (max-width: 550px){input:focus{width:7em}content,.ErrorBox{width:100%;font-size:0.8rem}.Card{width:85vw;font-size:0.8em}.Card div{font-size:1.4em}.Card .DeadSign{font-size:0.8em;margin-top:0.2em;right:0.8em}header{font-size:0.7rem}}@media (max-width: 390px){.Nav{height:11rem}content,.ErrorBox,header{font-size:0.6em}}

